.bracket {
    max-width: 1020px;
    margin: 0 auto;
    background: #000;
    height: 450px;
    display: flex;
    flex-flow: row;
    justify-content: space-around;
    border-radius: 3px;
    .rounds {
        width: 132px;
        height: 100%;
        display: flex;
        flex-flow: column;
        justify-content: space-around;
        .match {
            .logos {
                display: flex;
                flex-flow: row wrap;
                position: relative;
                .team {
                    flex: 0 0 50%;
                    max-width: 50%;
                    background-color: #f2f2f2;
                    color: #000;
                    position: relative;
                    margin: 0;
                    font-weight: unset;
                    .seed {
                        color: #666;
                        font-size: 12px;
                        left: 0;
                        padding: 0 5%;
                        position: absolute;
                        top: 0; }
                    .logo {
                        img {
                            margin: 5px;
                            width: 40px;
                            display: block;
                            margin: 0 auto;
                            transition: 0.5s all; } }
                    [type=radio] {
                        position: absolute;
                        opacity: 0;
                        width: 0;
                        height: 0; }
                    [type=radio] + .logo>img {
                        cursor: pointer; }
                    &:first-child {
                        border-right: 0.5px solid #d8d8d8;
                        border-radius: 3px 0 0 0; }
                    &:last-child {
                        border-left: 0.5px solid #d8d8d8;
                        border-radius: 0 3px 0 0;
                        .seed {
                            left: unset;
                            right: 0; } } } }
            .summary {
                display: flex;
                flex-flow: row wrap;
                background-color: #fff;
                color: #000;
                border-radius: 0 0 3px 3px;
                border-top: 1px solid #d8d8d8;
                font-size: 12px;
                line-height: 21px;
                .team, .scores {
                    flex: 0 0 33.33333%;
                    max-width: 33.33333%;
                    text-align: center;
                    text-transform: uppercase; }
                .scores {
                    font-size: 16px;
                    input[type='number'] {
                        height: 18px;
                        width: 18px; } } }
            &.voted {
                .team {
                    .logo {
                        img {
                            opacity: 0.1; } }

                    &.pick {
                        [type=radio]:checked + .logo>img {
                            opacity: 1; } } } } } } }

@media screen and (max-width: 1023px) {
    .bracket {
        flex-flow: column;
        height: unset;
        width: 100%;
        padding: 5px;
        .rounds {
            width: 100%;
            flex-flow: row;
            margin: 3% 0;
            .match {
                flex: 0 0 calc(25% - 3px);
                max-width: calc(25% - 3px);
                margin: 0 1.5px;
                .logos {
                    .team {
                        .seed {
                            font-size: 3vw; }
                        .logo {
                            img {
                                margin: 1%;
                                width: 98%; } } } }
                .summary {
                    font-size: 3vw;
                    line-height: 1.2;
                    .scores {
                        font-size: 3vw; } } } } } }

.round1.west {
    order: 1; }
.round2.west {
    order: 2; }
.round3.west {
    order: 3; }
.finals {
    order: 4; }
.round3.east {
    order: 5; }
.round2.east {
    order: 6; }
.round1.east {
    order: 7; }

input::-webkit-outer-spin-button,input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }
